<template>
  <div class="my-checkbox-group">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'myCheckboxGroup',
  data() {
    return {
      options: [], // 
    }
  },
  props: {
    // 
    value: {
      type: Array,
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // -1
    max: {
      type: Number,
      default: -1,
    },
    // -1
    min: {
      type: Number,
      default: -1,
    },
    // 
    button: {
      type: Boolean,
      default: false,
    },
  },
  watch: {
    value: {
      handler(newVal) {
        this.initValue()
      },
      immediate: true,
    },
    // 
    disabled: {
      handler(newVal) {
        this.syncOptionsDisable(newVal)
      },
      immediate: true,
    },
    // 
    button: {
      handler(newVal) {
        this.syncOptionsButtonStyle(newVal)
      },
      immediate: true,
    },
  },
  created() {
    // on-checkbox-addcheckboxoptions
    this.$on('on-checkbox-add', (checkbox) => {
      this.options.push(checkbox)
      this.initValue()
      this.syncOptionsDisable(this.disabled)
      this.syncOptionsButtonStyle(this.button)
    })
    // on-checkbox-removecheckboxoptions
    this.$on('on-checkbox-remove', (checkbox) => {
      this.options.splice(this.options.indexOf(checkbox), 1)
      this.syncValue()
    })
    // checkboxvalue
    this.$on('on-checkbox-select', () => {
      this.syncValue()
    })
  },
  methods: {
    initValue() {
      this.options.forEach((cell) => {
        cell.checked = this.value.find((d) => d === cell.label)
      })
      this.$nextTick(() => {
        this.optionsLimit()
      })
    },
    /**
     * @description value
     */
    syncValue() {
      let currentValue = this.options.reduce((currentArr, cell) => {
        cell.checked && currentArr.push(cell.label)
        return currentArr
      }, [])
      this.$emit('input', currentValue)
      this.$emit("change", currentValue);
      this.optionsLimit()
    },
    /**
     * @description myDisabled
     * @param {Boolean} disabled 
     */
    syncOptionsDisable(disabled) {
      this.options.forEach((d) => {
        d.myDisabled = disabled
      })
    },
    /**
     * @description 
     */
    optionsLimit() {
      this.$nextTick(() => {
        this.options.forEach((cell) => {
          cell.limitDisabled = false
        })

        const length = this.value.length
        // 
        if (length >= this.max && this.max > -1) {
          this.options.forEach((cell) => {
            if (!cell.checked) {
              cell.limitDisabled = true
            }
          })
        }
        // 
        if (length <= this.min && this.min > -1) {
          this.options.forEach((cell) => {
            if (cell.checked) {
              cell.limitDisabled = true
            }
          })
        }
      })
    },
    /**
     * @description button
     * @param {Boolean} value 
     */
    syncOptionsButtonStyle(value) {
      this.options.forEach((d) => {
        d.button = value
      })
    },
  },
}
</script>
