<template>
  <div
    class="my-input"
    :class="{ 'my-input-disabled': disabled, [`my-input-${size}-size`]: true }"
    ref="myInput"
  >
    <input
      class="my-input-input"
      :class="{
        'my-input-input-disabled': disabled,
        'my-input-input-icon': needPaddingRight,
        'my-input-input-icon-suffix': prefixIcon,
        [`my-input-input-${size}-size`]: true,
      }"
      type="text"
      :value="currentValue"
      @input="handleInput"
      @focus="handleFocus"
      :readonly="readonly"
      :placeholder="placeholder"
      :disabled="disabled"
    />
    <!--  -->
    <span class="my-input-icon my-input-icon-prefix">
      <i
        class="iconfont"
        :class="{
          [`${prefixIcon}`]: prefixIcon,
        }"
      />
    </span>
    <!--  -->
    <span class="my-input-icon">
      <i
        class="iconfont icon-close"
        v-if="clearable && currentValue && !disabled"
        @click="handleClean"
      />
      <i
        v-else
        class="iconfont"
        :class="{
          [`${suffixIcon}`]: suffixIcon,
        }"
      />
    </span>
    <!--  -->
    <transition name="fade-bottom">
      <div
        class="my-input-suggestion"
        v-show="panelVisible"
        ref="mySuggestion"
        v-if="suggestion && this.options.length > 0"
      >
        <div
          class="my-input-suggestion-cell"
          @click="setSuggestion(item.value)"
          v-for="item in options"
          :key="item.value"
        >
          <slot :item="item">
            {{ item.value }}
          </slot>
        </div>
        <!--  -->
        <div class="my-input-suggestion-layer" v-show="loading">
          <i class="iconfont icon-loading my-input-suggestion-layer-loading" />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
// 
import { oneOf, debounce } from '../../utils/assist'

export default {
  name: 'MyInput',
  data() {
    return {
      currentValue: this.value, // 
      panelVisible: false, // hover
      options: [], // 
      loading: false, // 
    }
  },
  props: {
    value: {
      type: String,
      default: '',
    },
    // 
    placeholder: {
      type: String,
      default: '',
    },
    max: {
      type: Number,
    },
    min: {
      type: Number,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    // 
    disabled: {
      type: Boolean,
      default: false,
    },
    // 
    clearable: {
      type: Boolean,
      default: false,
    },
    // 
    prefixIcon: {
      type: String,
      default: '',
    },
    // 
    suffixIcon: {
      type: String,
      default: '',
    },
    // 
    size: {
      validator(value) {
        return oneOf(value, ['large', 'medium', 'small', 'mini'])
      },
      type: String,
      default: 'medium',
    },
    // 
    suggestion: {
      type: Boolean,
      default: false,
    },
    // 
    fetchSuggestions: {
      type: Function,
    },
  },
  watch: {
    value: {
      handler(newVal) {
        this.currentValue = newVal
      },
      immediate: true,
    },
  },
  computed: {
    // 
    needPaddingRight() {
      return this.clearable && this.suffixIcon
    },
  },
  mounted() {
    // 
    document.addEventListener('click', this.addCloseEvent)
    this.getSuggesitions()
  },
  beforeDestroy() {
    // 
    document.removeEventListener('click', this.addCloseEvent)
  },
  methods: {
    /**
     * @description 
     */
    handleInput(_e) {
      const value = _e.target.value
      this.currentValue = value
      this.$emit('input', value)
      this.$emit('change', value)
      debounce(
        () => {
          this.getSuggesitions()
        },
        333,
        'fetch-suggestions'
      )
    },
    /**
     * @description 
     */
    handleClean() {
      if (!this.disabled) {
        this.currentValue = ''
        this.$emit('input', this.currentValue)
        this.$emit('change', this.currentValue)
        this.getSuggesitions()
      }
    },
    /**
     * @description inputhover
     */
    handleFocus() {
      this.panelVisible = true
    },
    /**
     * @description /
     */
    addCloseEvent(event) {
      // 
      let target = event.path.find((d) => d === this.$refs.myInput)
      if (!target && this.panelVisible) {
        this.panelVisible = false
      }
    },
    /**
     * @description 
     */
    getSuggesitions() {
      if (this.fetchSuggestions) {
        this.loading = true
        this.fetchSuggestions(this.currentValue, (options) => {
          this.loading = false
          this.options = options
        })
      }
    },
    /**
     * @description 
     */
    setSuggestion(str) {
      this.currentValue = str
      this.$emit('input', this.currentValue)
      this.$emit('change', this.currentValue)
      this.panelVisible = false
    },
  },
}
</script>
